<template>
  <div class="bloc l-bloc " id="bloc-0">
    <div class="container bloc-lg">
      <div class="row">
        <div class="col-md-8">
          <img src="../../assets/contact.jpg" class="img-fluid mx-auto d-block img-style" />
        </div>
        <div class="col-md-4 align-self-center">
          <h4 class="mg-lg text-center" style="color: #be0819; font-weight: bold;">
            Want to know more about MCSS?
          </h4>
          <h5 class="mg-lg text-center" style="color: #be0819">
            Looking for more awesome events?
          </h5>
          <p class="p-style mg-lg text-center">
            Make sure to follow us on our social media!
          </p>
          <div class="row row-style">
            <div class="col">
              <div class="text-center">
                <i class="fab fa-facebook icon-md" @click="toFB"></i>
              </div>
            </div>
            <div class="col">
              <div class="text-center">
                <i class="fab fa-instagram icon-md" @click="toIns"></i>
              </div>
            </div>
            <div class="col">
              <div class="text-center">
                  <i class="fab fa-weixin icon-md" @click="showModal" ref="btnShow"></i>
                  <b-modal id="modal1" @hidden="onHidden">
                    <h2>Follow us on WeChat</h2>
                    <div class="d-block"><img src="../../assets/qrCode.jpg" alt="wechatMP"></div>
                  </b-modal>
              </div>
            </div>
            <div class="col">
              <div class="text-center">
                <i class="fas fa-envelope-square icon-md" @click="toMail"></i>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
      name: 'Contact',
      methods: {
        showModal () {
          this.$root.$emit('bv::show::modal', 'modal1')
        },
        onHidden (evt) {
          // Return focus to our Open Modal button
          // See accessibility below for additional return-focus methods
          this.$refs.btnShow.$el.focus()
        },
        toFB () {
          location.href = 'https://www.facebook.com/mcss.ca/'
        },
        toIns () {
          location.href = 'https://www.instagram.com/mcssfam/?hl=en'
        },
        toMail () {
          location.href = 'mailto:vpexternal@mcss.ca'
        }
      }
    }
</script>

<style scoped>

  html{overflow-x: hidden;}

  body{
    margin:0;
    padding:0;
    background:#FFFFFF;
    overflow-x:hidden;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  a,button{transition: background .3s ease-in-out;outline: none!important;} /* Prevent ugly blue glow on chrome and safari */
  a:hover{text-decoration: none; cursor:pointer;}

  .fa-facebook:hover {
    color:#2C6A93;
    cursor: pointer;
  }

  .fa-instagram:hover {
    color: #DD2A7B;
    cursor: pointer;
  }

  .fa-weixin:hover {
    cursor: pointer;
    color: rgb(9, 184, 62);
  }
  
  .fa-envelope-square:hover {
    cursor: pointer;
    color: #bf0819;
  }
  
  /* = Blocs
  -------------------------------------------------------------- */

  .bloc{
    width:100%;
    clear:both;
    background: 50% 50% no-repeat;
    padding:0 50px;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    position:relative;
    display:flex;
  }
  .bloc .container{
    padding-left:0;
    padding-right:0;
  }
  
  /* Sizes */

  .bloc-lg{
    padding:100px 50px;
  }


  /* Light theme */

  .d-bloc .card,.l-bloc{
    color:rgba(0,0,0,.5);
  }
  .d-bloc .card button:hover,.l-bloc button:hover{
    color:rgba(0,0,0,.7);
  }
  .l-bloc .icon-round,.l-bloc .icon-square,.l-bloc .icon-rounded,.l-bloc .icon-semi-rounded-a,.l-bloc .icon-semi-rounded-b{
    border-color:rgba(0,0,0,.7);
  }
  .d-bloc .card .divider-h span,.l-bloc .divider-h span{
    border-color:rgba(0,0,0,.1);
  }
  .d-bloc .card .a-btn,.l-bloc .a-btn,.l-bloc .navbar a,.l-bloc a .icon-sm, .l-bloc a .icon-md, .l-bloc a .icon-lg, .l-bloc a .icon-xl, .l-bloc h1 a, .l-bloc h2 a, .l-bloc h3 a, .l-bloc h4 a, .l-bloc h5 a, .l-bloc h6 a, .l-bloc p a{
    color:rgba(0,0,0,.6);
  }
  .d-bloc .card .a-btn:hover,.l-bloc .a-btn:hover,.l-bloc .navbar a:hover, .l-bloc a:hover .icon-sm, .l-bloc a:hover .icon-md, .l-bloc a:hover .icon-lg, .l-bloc a:hover .icon-xl, .l-bloc h1 a:hover, .l-bloc h2 a:hover, .l-bloc h3 a:hover, .l-bloc h4 a:hover, .l-bloc h5 a:hover, .l-bloc h6 a:hover, .l-bloc p a:hover{
    color:rgba(0,0,0,1);
  }
  .l-bloc .navbar-toggle .icon-bar{
    color:rgba(0,0,0,.6);
  }
  .d-bloc .card .btn-wire,.d-bloc .card .btn-wire:hover,.l-bloc .btn-wire,.l-bloc .btn-wire:hover{
    color:rgba(0,0,0,.7);
    border-color:rgba(0,0,0,.3);
  }


  /* = NavBar
  -------------------------------------------------------------- */

  .hero .hero-nav{
    padding-left:inherit;
    padding-right:inherit;
  }
  /* Handle Multi Level Navigation */.navbar-light .navbar-nav .nav-link {
                                       color: rgb(64, 64, 64);
                                     }
  .btco-menu li > a {
    padding: 10px 15px;
    color: #000;
  }
  .btco-menu .active a:focus,.btco-menu li a:focus ,.navbar > .show > a:focus{
    background: transparent;
    outline: 0;
  }
  .dropdown-menu .show > .dropdown-toggle::after{
    transform: rotate(-90deg);
  }

  /* = Bric adjustment margins
  -------------------------------------------------------------- */

  .mg-lg{
    margin-top:10px;
    margin-bottom:40px;
  }

  /* = Buttons
  -------------------------------------------------------------- */

  .btn-d,.btn-d:hover,.btn-d:focus{
    color:#FFF;
    background:rgba(0,0,0,.3);
  }

  /* Prevent ugly blue glow on chrome and safari */
  button{
    outline: none!important;
  }


  /* = Icons
  -------------------------------------------------------------- */
  .icon-md{
    font-size:30px!important;
  }

  .card-sq, .card-sq .card-header, .card-sq .card-footer{
    border-radius:0;
  }
  .card-rd{
    border-radius:30px;
  }
  .card-rd .card-header{
    border-radius:29px 29px 0 0;
  }
  .card-rd .card-footer{
    border-radius:0 0 29px 29px;
  }


  /* ScrollToTop button */

  .scrollToTop span{
    margin-top: 6px;
  }

  /* = Custom Styling
  -------------------------------------------------------------- */

  h1,h2,h3,h4,h5,h6,p,label,.btn,a{
    font-family:"Helvetica";
  }
  .container{
    max-width:1140px;
  }
  .img-style{
    width:553px;
    border-radius:8px 8px 8px 8px;
  }
  .p-style{
    font-size:20px;
  }
  .row-style{
    width:100%;
  }

  /* = Mobile adjustments
  -------------------------------------------------------------- */
  @media (max-width: 1024px)
  {
    .bloc{
      padding-left: 20px;
      padding-right: 20px;
    }
    .bloc.full-width-bloc, .bloc-tile-2.full-width-bloc .container, .bloc-tile-3.full-width-bloc .container, .bloc-tile-4.full-width-bloc .container{
      padding-left: 0;
      padding-right: 0;
    }
  }
  @media only screen and (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : landscape) {.b-parallax{background-attachment:scroll;}}
  @media only screen and (min-device-width: 1024px) and (max-device-width: 1366px) and (-webkit-min-device-pixel-ratio: 1.5) {.b-parallax{background-attachment:scroll;}}
  @media (max-width: 991px)
  {
    .container{width:100%;}
    .b-parallax{background-attachment:scroll;}
    .page-container, #hero-bloc{overflow-x: hidden;position: relative;} /* Prevent unwanted side scroll on mobile */
    .bloc{padding-left: constant(safe-area-inset-left);padding-right: constant(safe-area-inset-right);} /* iPhone X Notch Support*/
    .bloc-group, .bloc-group .bloc{display:block;width:100%;}
  }
  @media (max-width: 767px)
  {
    .page-container{
      overflow-x: hidden;
      position:relative;
    }
    .b-parallax{
      background-attachment:scroll;
    }

    .bloc-tile-2 .container, .bloc-tile-3 .container, .bloc-tile-4 .container{
      padding-left:0;padding-right:0;
    }
    .a-block{
      padding:0 10px;
    }
    .btn-dwn{
      display:none;
    }
    .voffset{
      margin-top:5px;
    }
    .voffset-md{
      margin-top:20px;
    }
    .voffset-lg{
      margin-top:30px;
    }
    form{
      padding:5px;
    }
    .close-lightbox{
      display:inline-block;
    }
    .blocsapp-device-iphone5{
      background-size: 216px 425px;
      padding-top:60px;
      width:216px;
      height:425px;
    }
    .blocsapp-device-iphone5 img{
      width: 180px;
      height: 320px;
    }
  }

</style>



