/* eslint-disable comma-dangle,object-property-newline */
import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home.vue'
import Nav from '../components/Nav.vue'
import Events from '../components/items/Events.vue'
import About from '../components/items/About.vue'
import Sponsors from '../components/items/Sponsors.vue'
import Contact from '../components/items/Contact.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home, About, Sponsors
    },
    {
      path: '/events',
      name: 'nav',
      component: Nav,
      children: [
        {path: '/', name: 'events', component: Events},
        {path: '/about', name: 'about', component: About},
        {path: '/sponsors', name: 'sponsors', component: Sponsors},
        {path: '/contact', name: 'contact', component: Contact}
      ]
    }
  ]
})



