<template>
    <section>
        <header class="header">
            <div class="text-box">
                <h1 class="heading-primary">
                    <span class="heading-primary-main"><img src="../assets/white-logo.png" alt="Logo" class="logo"></span>
                    <span class="heading-primary-sub">McGill Chinese Students' Society</span>
                </h1>
                <a href="#" @click="navigateTo" class="btn btn-white btn-animated">Get Started</a>
            </div>
        </header>
    </section>
</template>

<script>
    export default {
      methods: {
        navigateTo () {
          this.$router.push({name: 'nav'})
        }
      }
    }
</script>

<style scoped>

    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }

    body {
        font-family: "Lato", sans-serif;
        font-weight: 400;
        font-size: 13px;
        line-height: 1.7;
        color: #777;
    }

    .header{
        height: 100vh;
        background-image: linear-gradient(to right bottom, rgba(190, 8, 51, 0.4), rgba(190, 8, 25, 0.4)),url(../assets/bg.jpg);
        background-size: cover;
        background-position: top left;
        position: relative;
    }

    .logo{
        height: 100px;
        color: white;
    }

    .text-box{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
    }

    .heading-primary{
        color: #fff;
        text-transform: uppercase;

        backface-visibility: hidden;
    }

    .heading-primary-main{
        display: block;
        font-size: 60px;
        font-weight: 800;
        letter-spacing: 0px;
        margin-bottom: 80px;

        animation-name: moveInLeft;
        animation-duration: 0.8s;
        animation-timing-function: ease-out;

        /* animation-iteration-count: 3 */
        /* animation-delay: 3s; */
    }

    .heading-primary-sub{
        display: block;
        font-size: 15px;
        font-weight: 700;
        line-height: 30px;
        letter-spacing: 8px;
        margin-bottom: 100px;

        animation-name: moveInRight;
        animation-duration: 0.8s;
        animation-timing-function: ease-out;
    }

    @keyframes moveInLeft {
        0%{
            opacity: 0;
            transform: translateX(-100px);
        }

        80%{
            transform: translateX(10px);
        }

        100%{
            opacity: 1;
            transform: translate(0);
        }
    }

    @keyframes moveInRight {
        0%{
            opacity: 0;
            transform: translateX(100px);
        }

        80%{
            transform: translateX(-10px);
        }

        100%{
            opacity: 1;
            transform: translate(0);
        }
    }

    @keyframes moveInBottom {
        0%{
            opacity: 0;
            transform: translateY(30px);
        }

        100%{
            opacity: 1;
            transform: translateY(0);
        }
    }

    .btn:link, .btn:visited{
        text-transform: uppercase;
        text-decoration: none;
        padding: 15px 40px;
        display: inline-block;
        border-radius: 100px;
        transition: all .2s;
        position: relative;
    }

    .btn:hover{
        transform: translateY(-3px);
        box-shadow: 0 10px 20px rgba(0, 0, 0, .3);
    }

    .btn:active{
        transform: translateY(-1px);
        box-shadow: 0 5px 10px rgba(0, 0, 0, .3);
    }

    .btn-white{
        background-color: #fff;
        color: #777;
    }

    .btn::after{
        content: "";
        display: inline-block;
        height: 100%;
        width: 100%;
        border-radius: 100px;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        transition: all .4s;
    }

    .btn-white::after{
        background-color: #fff;
    }

    .btn:hover::after{
        transform: scaleX(1.4) scaleY(1.6);
        opacity: 0;
    }

    .btn-animated{
        animation: moveInBottom .5s ease-out .75s;
        animation-fill-mode: backwards;
    }
</style>



