<template>
    <div>

      <b-navbar toggleable="sm" type="light" variant="">

        <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

        <router-link to="/" class="navbar-brand">
          <img width="110" height="40" src="../assets/red-logo.png" alt="">
        </router-link>

        <b-collapse is-nav id="nav_collapse">
          <ul class="navbar-nav ml-auto">
            <li class="nav-item">
              <router-link class="nav-link" to="/events">Events</router-link>
            </li>
            <li class="nav-item">
              <router-link class="nav-link" to="/about">About Us</router-link>
            </li>
            <li class="nav-item">
              <router-link class="nav-link" to="/sponsors">Sponsors</router-link>
            </li>
            <li class="nav-item">
              <router-link class="nav-link" to="/contact">Contact</router-link>
            </li>
          </ul>
        </b-collapse>

      </b-navbar>

        <div class="container">
          <transition name="third-party-animation"
                      leave-active-class="animated zoomOut"
                      enter-active-class="animated zoomIn" mode="out-in">
          <router-view></router-view>
          </transition>
        </div>

    </div>
</template>

<script>
    export default {
    }
</script>

<style scoped>

</style>



